.content {
  display: flex;
  flex-direction: column;
}

.content > .resources {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  padding: 0px;
  margin: 0px;
}

.resources > p {
  flex-grow: 1;
  padding: 0px;
  margin: 0px;
}

.content > .dashboard {
  flex-grow: 40;
}

.dashboard {
  padding: 16px;
}

.dashboard > button {
  border: 1px solid white;
  border-radius: 5em;
  padding: 10px;
  background-color: #16181D;
  color: white;
}